.App {
  text-align: center;
  height: 100vh;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
  margin-right:90%;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
  /*added */
  overflow: hidden; 
  background-color: #333; 
  position: fixed; 
  top: 0;
  width: 100%;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.backDropVisible{
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0; 
  background-color: #000; 
  opacity: .6;
  z-index: 2;
  -webkit-transition: opacity .4s;
  -o-transition: opacity .4s;
  transition: opacity .4s;
}

.backDropInvisible{
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0; 
  background-color: #000;
  opacity: 0; 
  z-index:-1;
  -webkit-transition: opacity .2s;
  -o-transition: opacity .2s;
  transition: opacity .2s;
}

.downArrowVisible{
  opacity: 1;
  position: absolute;
  left: 50%;
  bottom: 25px;
  color: #FFF;
  height: 10px;
  font-size: 25px;
  -webkit-animation: bounce 0.5s;
          animation: bounce 0.5s;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-transition: opacity 2s;
  -o-transition: opacity 2s;
  transition: opacity 2s;
}

.downArrowInvisible{
  opacity: 0;
  position: absolute;
  left: 50%;
  bottom: 25px;
  color: #FFF;
  height: 10px;
  font-size: 25px;
  -webkit-animation: bounce 0.5s;
          animation: bounce 0.5s;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-transition: opacity 2s;
  -o-transition: opacity 2s;
  transition: opacity 2s;
}

.iconBar {
  width: 35px;
  height: 5px;
  background-color: #FFF;
  margin: 6px 0;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
}

.hamburger{
  margin-left: 10px;
  width: 40px;
  height: 40px;
}

.tempIcon{
  -webkit-animation: hamburger-spin infinite 20s linear;
          animation: hamburger-spin infinite 20s linear;
  width: 40px;
  height: 40px;
}

.tempIconLetter{
  font-size: 40px;
  color: white;
}


.link-style{
  text-decoration: none;
  color: black;
  font-weight: bold;
}

.link-style:hover{
  text-decoration: none;
  color: blue;
  font-weight:bold;
}


.listStyle{ 
  text-align: center;
  list-style: none; 
  display: -ms-flexbox; 
  display: flex; 
  -ms-flex-direction: column; 
      flex-direction: column; 
  -ms-flex-pack: center; 
      justify-content: center; 
}

.main-text{
  position: absolute;
  top: 50%;
  left: 30%;
  text-align: center;
  color: #000;
  font-size: 27px;
  letter-spacing: 8px; 
  text-transform: uppercase;
  background-color: rgba(255,255,255,.9);
  padding: 10px;
  border-radius: 10px;
}

.open {
  background-color: #FFF;
  width: 40%;
  height: 100%;
  position: fixed;
  top: 0;
  z-index: 200;
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0);
}

.section1Styles { 
  /* The image used */
    background-image: url('https://placeimg.com/640/480/any/grayscale');
    background-color: white;

    /* Full height */
    height: 100%; 

    /* Create the parallax scrolling effect */
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }

  .section2Styles { 
    /* The image used */
    background-image: url('https://placeimg.com/640/480/any');
    background-color: white;

    /* Full height */
    height: 100%; 

    /* Create the parallax scrolling effect */
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }

  .section2-text{
    position: relative;
    top:50%;
    text-align: center;
    color: #000;
    font-size: 27px;
    letter-spacing: 8px; 
    text-transform: uppercase;
    background-color: rgba(255,255,255,.9);
    padding: 10px;
    border-radius: 10px;
  }

#skills-button{
  margin-top: 50%;
  left: 50%;
  position: absolute;
}

.sideMenuClose{
  background-color: #FFF;
  width: 40%;
  height: 100%;
  position: fixed;
  top:0;
  left:-50%;
  z-index: 200;
  -webkit-box-shadow: 2px 0px 5px rgba(0, 0, 0, 0.5);
          box-shadow: 2px 0px 5px rgba(0, 0, 0, 0.5);
  -webkit-transition: left 1.5s;
  -o-transition: left 1.5s;
  transition: left 1.5s;
}

.sideMenuOpen{
  background-color: #FFF;
  width: 40%;
  height: 100%;
  position: fixed;
  top:0;
  left:0;
  z-index: 200;
  -webkit-box-shadow: 2px 0px 5px rgba(0, 0, 0, 0.5);
          box-shadow: 2px 0px 5px rgba(0, 0, 0, 0.5);
  -webkit-transition: left 1.5s;
  -o-transition: left 1.5s;
  transition: left 1.5s;
}

.toggle-menu{
  z-index: 200;
  position: fixed;
}

.xbar { 
  width: 35px;
  height: 5px;
  background-color: #333;
  margin: 6px 0;
  -webkit-transition: opacity 0.4s;
  -o-transition: opacity 0.4s;
  transition: opacity 0.4s;
  -webkit-transform: rotate(-45deg) translate(-6px, 8px);
      -ms-transform: rotate(-45deg) translate(-6px, 8px);
          transform: rotate(-45deg) translate(-6px, 8px);
}

.xbar2 {
  width: 35px;
  height: 5px;
  background-color: #333;
  margin: 6px 0;
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
  -webkit-transform: rotate(45deg) translate(0px, 0px);
      -ms-transform: rotate(45deg) translate(0px, 0px);
          transform: rotate(45deg) translate(0px, 0px);
}






 


h1{
  color: blue;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@-webkit-keyframes hamburger-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes hamburger-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@-webkit-keyframes bounce {
  from {tranform: translate3d(0,0,0);}
  to   { -webkit-transform: translate3d(0,10px, 0); transform: translate3d(0,10px, 0); }
}

@keyframes bounce {
  from {tranform: translate3d(0,0,0);}
  to   { -webkit-transform: translate3d(0,10px, 0); transform: translate3d(0,10px, 0); }
}


